.category-container-root {
    animation: group-opacity 0.5s ease-out;
}

.category-left {
    width: 20%;
    min-width: 240px;
}

.category-right {
    width: 80%;
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 768px) {
    .category-left {
        display: none;
    }

    .category-right {
        width: 100%;
    }
}