
.child-category-element-container {
    /*display: grid;*/
    /*grid-template-columns: auto auto auto auto auto;*/
    /*grid-column-gap: 5px;*/
    /*grid-row-gap: 5px;*/
    /*display: flex;*/
    /*width: 100%;*/
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    /*grid-column-gap: 5px;*/
    /*grid-row-gap: 5px;*/
    /*background-color: white;*/
    grid-column-gap: 1em;
    grid-row-gap: 1em;
    margin-top: 1em;
}

.child-category-title-container {
    background-color: white;
    position: relative;
    display: flex;
}

.child-category-title {
    padding: 1.2em 1em 1.2em 0em;
    font-size: 1.2em;
    font-weight: bold;
}

.child-category-select-container {
    padding-top: 1em;
}

.child-category-select {
    font-size: 0.9em;
    padding: 5px;
    border: 1px solid #e0e0e0;
    background-color: white;
}

.child-category-select > option {
    padding: 5px;
    font-size: 15px;
}

.child-category-select:focus {
    outline: none;
}

.child-category-element {
    width: 100%;
    height: 100%;
    align-self: center;
    box-sizing: border-box;
}

.child-category-element:hover {
    /*-webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.5);*/
    /*-moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.5);*/
    /*box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.5);*/
    z-index: 3;
}

.child-category-loading-container {
    width: 100%;
    position: relative;
}
.child-category-loading-wrapper {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
}

@media screen and (max-width: 1280px){
    .child-category-element-container {
        grid-template-columns: 1fr 1fr 1fr;
        box-sizing: border-box;
        width: 100%;
    }

}

@media screen and (max-width: 768px) {
    .child-category-element-container {
        grid-template-columns: 1fr 1fr;
        box-sizing: border-box;
        width: 100%;
    }
    .child-category-title-container {
        display: flex;
        flex-direction: column;
    }
    .child-category-select-container{ padding-top: 0}
}