.filter-component-container {
    display: flex;
    flex-direction: column;
    margin-right: 1em;
    margin-top: 1em;
}
.filter-component-menu{
    background-color: white;
    margin-bottom: 2em;
}
.filter-component-menu-title{
    padding: 1em;
    background-color: var(--main-bg-color);
    font-weight: bold;
    color: white;
}
.filter-component-menu-body {
    border-left: 1px solid rgba(0,0,0, 0.1);
    border-right: 1px solid rgba(0,0,0, 0.1);
}

.filter-component-title-container {
    padding: 1em;
    color: white;
    background-color: var(--main-bg-color);
}

.filter-component-title {
    font-weight: bold;
    margin-left: 1em;
}

.filter-component-input {
    display: flex;
    justify-content: space-between;
    margin-top: 1em;
}

.filter-component-input div {
    width: 40%;
}

.filter-component-container div input {
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #e0e0e0;
    padding: 0.5em;
}

.filter-component-container div input:focus {
    outline: none;
}

.filter-component-input-divider {
    width: 20%;
    text-align: center;
    align-items: center;
    margin-top: 0.3em;
}
.filter-component-checkbox {
    display: flex;
    margin-top:1em;
}
.filter-component-checkbox-1 {
    max-width: 2em;
}
.filter-component-type-container {
    padding: 1em;
    border: 1px solid rgba(0,0,0,0.1);
}
.filter-component-price-container {
    padding: 1em;
    border: 1px solid rgba(0,0,0,0.1);
    border-top: none;
}
.filter-component-button {
    border: 1px solid var(--main-bg-color);
    color: white;
    margin-top: 1em;
    padding: 0.5em;
    background-color: var(--main-bg-color);
}
.filter-component-banner {
    object-fit: contain;
    width: 100%;
}